const ScrollBackground = () => {

    // Gradient speed
    const gradSpeed = 0.5;
    
    // Initial background gradient position
    const [ gradPos, setGradPos ] = useState(0);

    // New gradient position
    function updateGradPos(){
        setGradPos((window.scrollY * gradSpeed) % window.innerWidth);
    }

    useEffect(() => {
        window.addEventListener('scroll', updateGradPos);
        window.addEventListener('resize', updateGradPos);
    }, []);
    

    
    return (
        <>
            <div className="app-bg-grad no-print"
            style={{ transform: `translate3d(${gradPos}px, 0, 0)` }}>
            </div>
            <div className="app-bg-cover no-print">
                { bgCoverImg }
            </div>
        </>
    );
}
 
export default ScrollBackground;
